(function (define) {
    'use strict';

    define(function (require) {

        var mime, when, registry;

        mime = require('../interceptor/base/mime');
        when = require('../../when/promise');

        function Registry(mimes){

            /**
             * MIME TYPE
             *
             * @param {string} MIME TYPE
             * @return promise
             */
            this.lookup = function (type) {
                var parsed;

                parsed = typeof type === 'string' ? mime.parse(type) : type;

                if (mimes[parsed.raw]) {
                    return mimes[parsed.raw];
                }
                if (mimes[parsed.type + parsed.suffix]) {
                    return mimes[parsed.type + parsed.suffix];
                }
                if (mimes[parsed.type]) {
                    return mimes[parsed.type];
                }
                if (mimes[parsed.suffix]) {
                    return mimes[parsed.suffix];
                }

                // MIMEreject
                return when.reject(new Error('Unable to locate converter for mime "' + parsed.raw + '"'));
            };

            /**
             * MIME TYPE,@extends Promise
             *
             * @param {string} MIME TYPE
             * @param [Converter] 
             * @return promiseConverter
             *
             */
            this.register = function (type, converter) {
                // 2016-04-03 when.js
                //mimes[type] = when(converter);
                mimes[type] = when.resolve(converter);
                return mimes[type];
            };

        }

        registry = new Registry({});

        // serializers
        registry.register('application/json', require('./type/application/json'));

        return registry;
    });

}(
    typeof define === 'function' && define.amd ? define : function (factory) {
        module.exports = factory(require);
    }
    // Boilerplate for AMD and Node
));


