(function (define) {
    'use strict';

    define(function (require) {

        var defaultClient, responsePromise, client, when, mixin;

        defaultClient = require('./client/base/default');
        responsePromise = require('./util/responsePromise');
        client = require('./client/base/client');
        when = require('../when/promise');
        mixin = require('./util/mixin');

        /**
         * 
         *
         * @param {Client} [target] 
         * @param {Object} [config] 
         * @returns {Client} 
         *
         * @class Interceptor
         */

        function defaultInitHandler(config) {
            return config;
        }

        function defaultRequestHandler(request /*, config, meta */) {
            return request;
        }

        function defaultResponseHandler(response /*, config, meta */) {
            return response;
        }

        //function race(promisesOrValues) {
        //    return when.promise(function (resolve, reject) {
        //        promisesOrValues.forEach(function (promiseOrValue) {
        //            when(promiseOrValue, resolve, reject);
        //        });
        //    });
        //}

        /**
         * 
         * @param properties 
         * @returns {ComplexRequest}
         * @constructor
         */
        function ComplexRequest(properties) {
            if (!(this instanceof ComplexRequest)) {
                return new ComplexRequest(properties);
            }
            mixin(this, properties);
        }

        /**
         * 
         *
         * @param {Function} [handlers.init] 
         * @param {Function} [handlers.request] 
         * @param {Function} [handlers.response] 
         * @param {Function} [handlers.success] 
         * @param {Function} [handlers.error] 
         * @param {Function} [handlers.client] 
         *
         * @returns {Interceptor}
         */
        function interceptor(handlers) {

            var initHandler, requestHandler, successResponseHandler, errorResponseHandler;

            handlers = handlers || {};

            // wrap
            initHandler            = handlers.init    || defaultInitHandler;
            requestHandler         = handlers.request || defaultRequestHandler;
            successResponseHandler = handlers.success || handlers.response || defaultResponseHandler;
            errorResponseHandler   = handlers.error   || function () {
                    // 2016-04-03 when.js
                    //return when((handlers.response || defaultResponseHandler).apply(this, arguments), when.reject, when.reject);
                    return when.reject((handlers.response || defaultResponseHandler).apply(this, arguments));
                };

            // 
            return function (target, config) {

                // 
                if (typeof target === 'object') {
                    config = target;
                }
                if (typeof target !== 'function') {
                    target = handlers.client || defaultClient;
                }

                // 
                config = initHandler(config || {});

                // interceptor:,
                function interceptedClient(request) {
                    var context, meta;
                    context = {};
                    meta = { 'arguments': Array.prototype.slice.call(arguments), client: interceptedClient };
                    request = typeof request === 'string' ? { path: request } : request || {};
                    request.originator = request.originator || interceptedClient;
                    return responsePromise(
                        requestHandler.call(context, request, config, meta),
                        function (request) {
                            var response, abort, next;
                            next = target;
                            // 
                            if (request instanceof ComplexRequest) {
                                // defer
                                abort = request.abort;
                                next = request.client || next;
                                response = request.response;
                                request = request.request;
                            }

                            response = response || when.resolve(request).then(function (request) {
                                    return when.resolve(next(request)).then(function (response) {
                                        return successResponseHandler.call(context, response, config, meta);
                                    }, function (response) {
                                        return errorResponseHandler.call(context, response, config, meta);
                                    });
                                });
                            return abort ? when.race([response, abort]) : response;
                        },
                        function (error) {
                            //return when.reject({ request: request, error: error });
                            return when.reject(error);
                        }
                    );
                }

                return client(interceptedClient, target);
            };
        }

        interceptor.ComplexRequest = ComplexRequest;

        interceptor.check = function (response) {
            if(response.error){
                return when.reject(response);
            }
        };

        return interceptor;

    });

}(
    typeof define === 'function' && define.amd ? define : function (factory) {
        module.exports = factory(require);
    }
    // Boilerplate for AMD and Node
));


